.header {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 114px;
  padding-top: 10px;
  margin: auto;
  z-index: 100;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-menu {
  display: flex;
  margin-right: 36px;
}
.header-menu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 43px;
  padding-left: 0;
}
.header-menu::after {
  content: "";
  width: 1px;
  height: 22px;
  background-color: rgba(0, 0, 0, 0.2);
  transform: translate(18px, 13px);
}

.header-phones {
  font-weight: 600;
  color: var(--font-black);
}

.header-search {
  cursor: pointer;
}

.header-mobile {
  position: absolute;
  z-index: 100;
  flex-direction: column;
  width: 100%;
  background-color: #fff;
  right: 0;
  display: none;
}
.header-mobile .header-menu ul {
  flex-direction: column;
}

.menu-button-container {
  display: none;
  height: 100%;
  width: 30px;
  height: 30px;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 16px;
}

#menu-toggle {
  display: none;
}

.menu-button,
.menu-button::before,
.menu-button::after {
  display: block;
  background-color: var(--font-gray);
  position: absolute;
  height: 4px;
  width: 30px;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 2px;
}

.menu-button::before {
  content: "";
  margin-top: -8px;
}

.menu-button::after {
  content: "";
  margin-top: 8px;
}

#menu-toggle:checked + .menu-button-container .menu-button::before {
  margin-top: 0px;
  transform: rotate(405deg);
}

#menu-toggle:checked + .menu-button-container .menu-button {
  background: rgba(255, 255, 255, 0);
}

#menu-toggle:checked + .menu-button-container .menu-button::after {
  margin-top: 0px;
  transform: rotate(-405deg);
}

.menu-checked::before {
  margin-top: 0px;
  transform: rotate(405deg);
}
.menu-checked::after {
  margin-top: 0px;
  transform: rotate(-405deg);
}

@media screen and (max-width: 1260px) {
  .header-menu ul {
    gap: 15px;
  }
}
@media screen and (max-width: 1150px) {
  .header {
    position: fixed;
    background-color: var(--footer);
    width: 100%;
    top: 0;
  }
  .menu-button-container {
    display: flex;
  }
  .header-right {
    justify-content: space-between;
  }
  .header-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  .header-menu::after {
    width: 0;
  }
  .header-menu ul {
    position: absolute;
    top: 0;
    margin-top: 114px;
    left: 0;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 0;
  }
  .header-menu ul li {
    height: 0;
    margin: 0;
    padding: 0;
    border: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
    overflow: hidden;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #222;
  }
  .header-menu ul li a {
    display: block;
    color: #fff;
  }
  .open-menu ul li {
    border: 1px solid #333;
    height: 2.5em;
    padding: 0.5em;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }
}
@media screen and (max-width: 480px) {
  .header .header-left {
    padding-left: 10px;
  }
  .header .header-right {
    padding-right: 10px;
  }
}